import React, { useEffect } from 'react'
import { CHAPIMAGE, TITLE2, UL, SUBTITLE } from '../css/css'
import { data } from '../../chapters'
import { urlbase, isLocalhost } from '../../index'


export const V04 = () => {

    const url = urlbase + data[4].url
    useEffect(() => {window.scrollTo(0, 0); if(!isLocalhost)fetch(url)}, [url])

    return (
    <div>

        <CHAPIMAGE src="/frontend-src/07/07.jpg" />

        <TITLE2> {data[4].number}. {data[4].title} </TITLE2>

        <UL> 1. Como una de las formas de estimular el consumo es bajar las tasas de interés mediante emisión monetaria, y esta baja de tasas también tendría que estimular la inversión, se necesita estudiar los temas por separado. </UL>

        <UL> 2. En este artículo 7 se analiza el efecto puro de estimular el consumo mediante "política fiscal" y por "política monetaria". Y en el artículo 17 se estudian la naturaleza y efectos de la "política monetaria" respecto a la inversión. </UL>


        <SUBTITLE> LA INVERSIÓN EN EL CENTRO<br/>DE LA DISCUSIÓN </SUBTITLE>

        <UL> 3. Estamos todos de acuerdo en que el crecimiento económico y la riqueza de las naciones son consecuencias de la inversión porque la inversión trae producción y empleo hoy, y aumentos de productividad e innovación mañana. </UL>

        <UL> 4. No obstante, el "sentido común" antes de la Gran Depresión (1929-1933) decía que la inversión es opuesta al consumo. Se exaltaban el ahorro y la austeridad para el logro del desarrollo. </UL>

        <UL> 5. Pero, desde esa crisis, el "sentido común" (socialista) dice que la inversión es una consecuencia del consumo. Según la idea que predomina actualmente, estimular el consumo mediante políticas fiscales y crediticias tendría que lograr un aumento de la inversión y, así, el crecimiento y la prosperidad. </UL>

        <UL> 6. ¿Realmente consumir mucho es el origen y la causa de la riqueza de las naciones? </UL>


        <SUBTITLE> DOS CARAS </SUBTITLE>

        <UL> 7. Cuando un ciudadano trabaja pero no consume todo lo que produce, ahorra. Cuando el ahorro lo deposita en un banco, invierte. Cuando una empresa toma ese capital y lo usa para financiar la búsqueda de productividad o de nuevos productos, invierte también. </UL>

        <UL> 8. Entonces, la razón de ser de la inversión es simplemente esta: Unos producen más que lo que consumen para que otros puedan consumir más de lo que producen, a cambio de una participación en los resultados. </UL>


        <SUBTITLE> LA CAJA </SUBTITLE>

        <UL> 9. ¿Cuál es el sentido de estimular el consumo? Se busca que los que "ahorran" prefieran consumir para que la mayor demanda de productos de consumo dé empleo a los desocupados y aumente la actividad en general. </UL>

        <UL> 10. ¿Pero es el ahorro una caja de la cual se pueda echar mano para financiar un aumento en el consumo? No lo es. </UL>

        <UL> 11. Hay que decir varias cosas sobre esto pero ya se puso sobre la mesa el elemento más determinante: Para el socialismo, el ahorro privado es un presupuesto que se puede usar para cumplir las promesas de campaña. </UL>

        <UL> 12. Hay dos dinámicas de ahorro posibles: Una en verdad es inversión, cuando se compra el derecho a acceder a una renta futura. La otra (atesoramiento) consiste en conservar un activo que no produce renta alguna, como un billete. </UL>

        <UL> 13. En el ámbito fiscal, hay 3 formas de estimular el consumo: con impuestos "progresivos", con transferencias de recursos financiadas con deuda del gobierno y con transferencias de recursos financiadas con emisión monetaria. </UL>


        <SUBTITLE> REASIGNACIÓN 1 </SUBTITLE>

        <UL> 14. La idea de que es expansivo quitarle parte del ingreso al ciudadano que va a ahorrar para dárselo al ciudadano que (por pobre) va a consumirlo inmediatamente al recibirlo, primeramente pasa por alto que el "ahorro" del aquel será en realidad inversión (ahorro bancario, etcétera). </UL>

        <UL> 15. De esa manera, no se logra un aumento del consumo a costa del ahorro sino de la inversión, porque ese dinero deja de financiar a una empresa que invierte en bienes de capital e innovaciones. Lo que causa es una reasignación de recursos, haciendo que las empresas se centren en producir bienes de consumo. Se pierde, entonces, crecimiento futuro por desarmarse la estructura de los puntos 8 y 9. </UL>

        <UL> 16. El argumento de Keynes de que la política fiscal redistributiva es expansiva de la producción es un error en el que se cae cuando se piensa que un ingreso ahorrado es un ingreso que cae en agujero negro para nunca más volver. </UL>

        <UL> 17. La excepción vendría dada cuando lo que el ciudadano despojado pensaba hacer era comprar moneda extranjera. Esta sí es una forma de ahorro de tipo agujero negro para el país. No obstante, en este caso tampoco es expansiva la "política fiscal" porque llega en un contexto de saqueo estatal generalizado contra los súbditos de su tiranía económica. </UL>


        <SUBTITLE> REASIGNACIÓN 2 </SUBTITLE>

        <UL> 18. Por otra parte, la forma de estimular el consumo que consiste en endeudar al Estado para regalar dinero, subsidiar servicios públicos o crear empleos públicos pasa por alto una verdad que al socialismo keynesiano siempre se le pasó por alto: La fuente de la cual el Estado toma créditos es la misma de la cual toman las empresas. </UL>

        <UL> 19. Cuando el Estado toma una gran porción del capital líquido de la sociedad, lo que queda para las empresas es poco y malo, por escasez. Nuevamente, lo que el gobierno socialista estimula de consumo es lo que se pierde de inversión; hay reasignación de recursos que atentará contra el crecimiento. </UL>

        <UL> 20. Y, de hecho, la pérdida de inversión es superior porque las expectativas que causa un Estado endeudado es de más impuestos, conflictividad social, inflación y corrupción. </UL>

        <UL> 21. No obstante, se argumenta que el Estado debe endeudarse para estimular el consumo cuando el contexto es muy malo, tan malo que de todos modos no va a haber inversión. Y que, de hecho, este impulso fiscal tiene que mejorar las expectativas. </UL>

        <UL> 22. Este argumento sería perfectamente válido si no fuera cierto (como lo es) que todas las crisis económicas son causadas por los Estados. </UL>


        <SUBTITLE> RECAPITULACIÓN </SUBTITLE>

        <UL> 23. Las políticas que estimulan el consumo hacen que las personas que trabajan produciendo bienes de capital o innovaciones abandonen sus funciones para dedicarse a producir bienes de consumo, que es lo que el mercado está demandando. </UL>

        <UL> 24. Los nuevos productos que no llegarán, los bienes de capital que no se producirán y las mejoras en la productividad que no se harán, los 3, marcarán el ritmo del estancamiento económico que vendrá después. </UL>

        <UL> 25. Y si se piensa en los desocupados: ellos dependen del crecimiento económico, no de un impulso cortoplacista que arruinará el crecimiento. Pero es hora de hablar de ese impulso cortoplacista. </UL>


        <SUBTITLE> AUGE EN LOS PRIMEROS AÑOS DE<br/>GOBIERNO SOCIALISTA </SUBTITLE>

        <UL> 26. Todo gobierno socialista (cuando se hace llamar progresista porque llegó al poder ganando elecciones) estimula el consumo mediante crédito barato, redistribución fiscal del ingreso, subsidios en tarifas de servicios públicos y aumentando el empleo público, en diferentes combinaciones. </UL>

        <UL> 27. Acto seguido, la renta nacional aumenta, los salarios reales aumentan, el Estado recauda más, las empresas venden bien, y parece que se ha encontrado la fórmula para el crecimiento con equidad gracias al papel activo del Estado en la economía. </UL>

        <UL> 28. El poder poético es elocuente también porque parece que quitarle a los que tienen más para darle a los que tienen menos ─postura política previa al análisis económico─ no tiene un costo económico sino todo lo contrario: es la clave del crecimiento. </UL>

        <UL> 29. El socialista saca pecho y se aferra a su caudillo redistribuidor, a quien le promete lealtad eterna, y proclama la victoria cientifica de la <i>teoría de la explotación</i>. </UL>

        <UL> 30. Cuando la economía colapse, se desatará la caza de brujas socialista contra las minorías "malvadas" que conspiran contra el pueblo: la oligarquía, el imperialismo, los especuladores, los judíos, los políticos opositores, la prensa u alguna otra innovación. </UL>

        <UL> 31. Pero ¿de dónde sale este auge transitorio? Se tiene que hablar de la diferencia entre ingreso y riqueza. </UL>


        <SUBTITLE> INGRESO Y RIQUEZA </SUBTITLE>

        <UL> 32. Cualquier persona con activos puede venderlos para aumentar su consumo porque un activo es un valor ahorrado, es decir, separado del consumo. Cualquiera puede vender su auto para viajar o comprar muchas cosas de consumo en los mercados; la cuestión será cómo seguirá su vida sin el auto. </UL>

        <UL> 33. De la misma manera, un país puede liquidar sus activos para lograr un aumento en la renta interna de este año (PBI o PIB) aunque signifique terminar con el crecimiento económico apartir del cuarto o quinto año, y para eso están las políticas "progresistas" del socialismo. Es la estimulación al consumo mediante, principalmente, crédito barato cuyo respaldo es la simple emisión monetaria. </UL>

        <UL> 34. Todo comienza con una circular del banco central que le comunica a los bancos comerciales que desde la fecha tienen permitido prestar más dinero que antes a sus clientes. Los bancos llaman a sus clientes y les ofrecen nuevos préstamos baratos, que se "depositan" en sus cuentas. El nuevo dinero nace en ese momento: cuando los bancos anotan que ahora sus clientes tienen más dinero (ver artículo 16). </UL>

        <UL> 35. Este nuevo dinero tira a la baja las tasas de interés que remuneran al ahorrista, empujando al ahorrista a llevarse el dinero de los bancos. ¿A dónde lo lleva? Hay 3 tipos de destino: el consumo, la compra de activos y la emigración a otro país. </UL>

        <UL> 36. Así, esta es la causa de que la emisión monetaria crediticia tenga un efecto expansivo cortoplacista: El ahorro en los bancos ─que es un activo y no forma parte de la renta interna de este año— se vuelca (parcialmente) al consumo —que sí integra el producto interno de este año─ (reasignación 3). </UL>

        <UL> 37. Se estimulan la producción, el empleo, los salarios, las ganancias y la recaudación del Estado a través de un ataque a los ahorristas bancarios. Y es que nadie está dispuesto a pagar por aquello que es gratis, y el insumo que usan los bancos —el dinero de los ahorristas— ahora llega gratis de parte del banco central. </UL>


        <SUBTITLE> SEGUNDA ETAPA </SUBTITLE>

        <UL> 38. ¿Pero luego qué? Cada vez son más los ahorros expulsados de la industria bancaria que buscan irse a otro país donde no se persiga a los ahorristas (reasignación 4). Esto causa un auge en las ventas de activos entranjeros —sobre todo billetes—, que se encarecen aceleradamente. La economía se contrae por los aumentos de costos de todo lo que sea importado. </UL>

        <UL> 39. El gobierno, por su parte, acelera la emisión, convencido de que su fórmula progresista es imbatible. La segunda etapa del "progresismo" se caracteriza por intentar seguir estimulando el consumo para reactivar la economía cuando ya no hay capitales que puedan ser liquidados. Entonces, la emisión crediticia no causa un cambio en las decisiones desde el ahorro local al consumo sino que todo empieza a irse al exterior. </UL>

        <UL> 40. La inflación aumenta, las reservas del banco central caen y el crecimiento sigue sin aparecer, al tiempo que los salarios reales caen y aumenta el malestar. Los casos de corrupción proliferan también porque en la primera etapa los dirigentes políticos y funcionarios del "progresismo" se creyeron invulnerables por el apoyo popular que tenían, y con derecho a premios por haber salvado al pueblo. </UL>


        <SUBTITLE> TERCERA ETAPA </SUBTITLE>

        <UL> 41. La tercera etapa es de racionamiento de divisas para seguir intentando lograr que capitales que no existen se vuelquen todos al consumo y ninguno al exterior, siempre buscando rememorar la primera etapa. </UL>

        <UL> 42. Consumido el capital, se agota el estímulo al consumo, no importa qué tan bajas sean las tasas de interés por emisión monetaria, cuánta redistribución del ingreso se haga, cuán baratos sean los servicios públicos o cuánto se endeude el Estado. </UL>

        <UL> 43. La misma lógica se da, obviamente, cuando el gobierno obliga a vender combustibles y servicios públicos a precios de pérdida: se vuelca al consumo el capital acumulado de las petroleras, las eléctricas y otras compañías, a costa del abastecimiento futuro. </UL>

        <UL> 44. El Estado socialista sale a robar a cara descubierta y se apodera de todo lo que pueda servirle: fondos para jubilaciones y pensiones, grandes empresas, tierras, depósitos. El FMI y el Banco Mundial se hacen presentes para prestar dinero para apoyar al socialismo. Hay autoritarismo, avasallamiento de instituciones, persecución a la prensa, compra de periodistas. </UL>


        <SUBTITLE> CUARTA ETAPA </SUBTITLE>

        <UL> 45. La cuarta y última etapa es una sola en economía, aunque es de lo más variada en política. En economía, el Estado se ve saturado de gastos, imposibilitado de aumentar la recaudación real y de tomar deuda, no queda ninguna caja importante que se pueda robar, y la recaudación real se pulveriza con la inflación, en un contexto de fuerte conflictividad social y política. </UL>

        <UL> 46. La emisión monetaria es lo único que queda. Se procede entonces a la desmonetización de la economía: No importa cuándo dinero emita el Estado, la inflación siempre va adelante porque la población, oprimida, huye de la moneda del Estado. Si el Estado emite 20% para cerrar su déficit, la inflación se acelera hasta 30%; si, entonces, el Estado emite 30%, la inflación se va a 50%; cuando, cansado, el Estado pone las imprentas a toda máquina, la hiperinflación aparece. (Ver el artículo 14.) </UL>

        <UL> 47. La recaudación real se le presenta al Estado socialista como una zanahoria frente a sus ojos pero atada a una vara en su cabeza, inalcanzable. </UL>

        <UL> 48. Como el producto interno medido en moneda nacional crece más rápido que la cantidad de dinero circulante, se da una desmonetización de la economía: cada vez hay menos dinero para mover lo que se produce. Los precios medidos en moneda extranjera se pulverizan. La economía está completamente quebrada. </UL>

        <UL> 49. Pero los desenlaces políticos son más variados y pocas veces se opera el ciclo completo; lo más común es que algo suceda durante la tercera etapa. Los nazis salieron a saquear Europa. Perón hizo un giro hacia la economía de mercado para sobrevivir políticamente. Los golpes de Estado y revoluciones también son desenlaces habituales. Pero, comúnmente, el "progresismo" pierde las elecciones y llega otra fuerza que suspende el saqueo estatal. </UL>

        <UL> 50. En realidad se necesita una dirigencia política muy arrogante y demasiado comprometida judicialmente para que la etapa de la desmonetización sea hiperinflacionaria. Los dirigentes socialistas más sabios prefieren, en silencio, recortar el gasto público y dejar de emitir, mientras siguen proclamando el discurso socialista. La dualidad discurso-realidad es una especialidad socialista muy destacada y uno nunca deja de sorprenderse. </UL>


        <SUBTITLE> SOCIALISMO </SUBTITLE>

        <UL> 51. Se ve cómo, derrotado en lo que tuvo que ver con la <i>ley del valor-trabajo</i>, el socialismo desde Keynes ha asumido una teoría del valor agregado donde es el consumidor al consumir quien genera el valor. Podemos llamarle <i>ley del valor-consumo</i> si quisiéramos ridiculizarlos, y será muy importante en el artículo siguiente (8). </UL>

        <UL> 52. Por otra parte, se resuelve así la paradoja de que la inversión aumente cuando se estimule el consumo en la primera etapa del ciclo. Aumenta la inversión como respuesta al aumento del consumo pero, a su vez, hay una gran destrucción de capital, que se vuelca al consumo. En otras palabras, si bien aumenta la inversión de este año, disminuye el stock de capital del país, formado en años anteriores, algo que el PBI no mide. </UL>

        <UL> 53. El socialismo keynesiano está convencido de que puede prescindir del ahorrista porque el Estado puede cumplir la misma función que ellos emitiendo dinero, usando al banco central. Plantea, entonces, una división funcional de la economía: el Estado como único capitalista, y los súbditos como trabajadores consumidores. El comunismo de siempre. Pero la guerra del gobierno socialista contra los ahorristas bancarios termina destruyéndolo a él mismo. </UL>

        <UL> 54. La base del asunto es que cerca de la mitad de la población de todo país se ve tentada a creer que consumir es bueno y ahorrar malo, como es fácil convencerlos de que hacer ejercicio es malo y que, de hecho, todo buen hábito es en verdad malo y todo mal hábito es en verdad bueno. </UL>

        <UL> 55. Sucede como en el cuento de la hormiga que pasa el verano trabajando mientras la cigarra toca la guitarra y canta pero luego llega el invierno. Así los países que respetan la propiedad privada y ahorran, por un lado, y los países que prefieren creerle a los socialista el cuento del consumo, por el otro. </UL>

        <UL> 56. No obstante, la verdad es una sola y la economía crece siguiendo ciclos ondulatorios. Por eso, el "progresista" llega al poder usualmente en el momento más bajo de un ciclo, cuando la gente está realmente cansada y busca algo nuevo. Así es como el socialista comienza a gobernar durante el primer tramo de un nuevo ciclo ascendente, que no se le debe a él ni hizo nada para conseguirlo, pero dota a sus políticas de una aparente virtud que sencillamente no tienen. Estuvo en el lugar justo en el momento justo. Pero ese es otro tema. </UL>


        <SUBTITLE> ORIGEN DEL DESARROLLO Y LA PROSPERIDAD </SUBTITLE>

        <UL> 57. ¿De dónde viene el crecimiento? La población crece, haciendo el trabajo más abundante, y nuevas ganancias son ahorradas e invertidas, haciendo al capital más abundante; así es como la producción crece. Pero esto es crecimiento del producto. </UL>

        <UL> 58. El crecimiento del producto <i>per capita</i> ─que es lo que conduce a la prosperidad─ solo se logra por aumento de productividad: reducción de costos ─usualmente por la implementación de tecnología─, nuevos productos y nuevas destrezas laborales. Y estas cosas se logran aumentando la inversión, no el consumo. </UL>

        <UL> 59. El ahorro es una etapa previa a la inversión; se necesita una población que ahorre y mecanismos institucionales privados para que el ahorro se haga capital. Y el único rol para el Estado en todo esto es garantizar la propiedad privada, la libertad, el cumplimiento de los contratos. </UL>


        <SUBTITLE> BURBUJA INMOBILIARIA </SUBTITLE>

        <UL> 60. Cuando el gobierno socialista ataca a los ahorristas bancarios falsificando dinero, una parte de los ahorros se vuelca al mercado inmobiliario, donde los ahorristas esperan conservar el valor de sus ahorros y tener un rendimiento por renta (reasignación 5). Al mismo tiempo, muchos toman crédito barato y lo vuelcan también al mercado inmobiliario. El resultado obvio es que la "estimulación del consumo con tasas de interés negativas" causa el fuerte encarecimiento de las viviendas. </UL>

        <UL> 61. Esta preferencia por "ahorrar" en viviendas hace que el índice de inflación no aumente al mismo ritmo que la emisión. Porque el índice de inflación es cuánto aumenta cierta "canasta" de bienes y servicios "representativa" del consumo popular, es decir, en qué se gastan los salarios. Y el costo de la vivienda tiene una pequeña participación en esa canasta, cuando la tiene. </UL>

        <UL> 62. No obstante, la industria de la construcción no acompaña al mismo ritmo los crecientes precios de las viviendas con más producción y más inversión en productividad. Los motivos básicos parecen ser tres: Primeramente, esta demanda inflada de viviendas tiene un origen artificial; depende de la inestable voluntad política y no de un proceso de mercado autónomo. Como siempre, no es realista esperar inversiones a mediano y largo plazo donde la demanda se puede acabar de un momento a otro. </UL>

        <UL> 63. Segundo, la construcción tiene problemas históricos para maquinizarse a causa de la naturaleza personalizada de los diseños arquitectónicos y la necesidad de trabajar en el terreno en vez de usar una fábrica, que impactan también en los tiempos de entrega. </UL>

        <UL> 64. Pero tercero y como factor más determinante, mucha gente se molesta con el auge de la construcción y exigen a los ayuntamientos que pongan límites y hasta prohiban construir, porque se creen con derecho a decidir sobre los bienes de los demás. Lo que sobreviene es un régimen donde el ayuntamiento planifica el mercado inmobiliario, para el show de la coima y el crecimiento acelerado de los precios. </UL>

        <UL> 65. La inversión no existe en un régimen de planificación estatal. La inversión existe bajo la expectativa de continuidad del negocio, pero la planificación existe bajo la imprevisible voluntad estatal. </UL>

        <UL> 66. La vivienda finalmente se hace inalcanzable y la gente deja de tener hijos. La población envejece y se hace de cultura socialista oligárquica, se opone a la inmigración, promueve la bicicleta, etcétera. La "política monetaria expansiva" y el estímulo al consumo siempre terminan mal. </UL>

    </div>
    )
}